import { Header } from './components/header';
import { searchPageReander } from './components/search';

const main = document.querySelector('main');

const mainRender = () => {
    Header.headerRoot.innerHTML = ``
    main.innerHTML = Header.headerInner;
};
mainRender();

// 랜더 함수
const renderContents = () => {
    const { pathname } = window.location;
    switch (pathname) {
        case '/':
            mainRender();
            break;
        case '/search':
            Header.headerRender();
            searchPageReander.searchRender();
            break;
        default:
            main.innerHTML = "<div>404</div>";
    }

    menuEventListener()
};

const menuEventListener = () => {
    const title = document.querySelector('.title');
    const menuList = document.querySelectorAll('.menu span');
    title.addEventListener("click", () => {
            const locationChangeEvent = new CustomEvent("locationchange", {
                composed: true,
                detail: { href: '/' },
            });
            
            window.dispatchEvent(locationChangeEvent);
    });
    menuList.forEach(e => {
        e.addEventListener('click', (e) => {
            let text = e.target.innerText;
            let hrefVal = '';

            if(text === 'Search' ) hrefVal = '/search'
            if(text === 'Movies' ) hrefVal = '/movies'
            if(text === 'About' ) hrefVal = '/about'

            const locationChangeEvent = new CustomEvent("locationchange", {
                composed: true,
                detail: { href: hrefVal },
            });
            
            window.dispatchEvent(locationChangeEvent);
        });
    });
}
menuEventListener();

const handleLocationChange = (e) => {
    const { href } = e.detail;
    
    window.history.pushState(undefined, "타이틀", href);
    renderContents();
};

window.addEventListener("locationchange", handleLocationChange);

window.addEventListener("popstate", () => {
    renderContents();
});



export { main }